<template>
  <div>
    <V-Scroll :isList="false" :scroll-height="`${mainHeight}px`">
      <div class="address-form">
        <div>
          <label>联络人</label>
          <input type="text" placeholder="名字">
        </div>
        <div>
          <label>手机号码</label>
          <input type="tel" placeholder="11位电话号码">
        </div>
        <div class="adderss-city">
          <label>选择地区</label>
          <input type="text" placeholder="地区信息">
        </div>
        <div class="address-textarea">
          <label>详细地址</label>
          <textarea placeholder="街道门牌"></textarea>
        </div>
        <div>
          <label>邮政编码</label>
          <input type="tel" placeholder="邮政编码">
        </div>
      </div>
      <!--<div class="address-checkBtn">-->
        <!--<p>设置默认地址</p>-->
        <!--<label class="ui-switch">-->
          <!--<input type="checkbox" checked="">-->
        <!--</label>-->
      <!--</div>-->
      <div class="address-save">保存</div>
    </V-Scroll>
  </div>
</template>

<script>
import Ajax from "@/mixins/ajax";

export default {
  name: "orderList",
  data() {
    return {
    };
  },
  mixins: [Ajax],
  components: {},
  mounted() {
  },
  methods: {}
};
</script>

<style scoped>
  .address-form{
    margin-top:.3rem;
  }
  .address-form>div{
    display: flex;
    justify-content: space-between;
    align-items: center;
    height:1.5rem;
    padding:0 .3rem;
    border-bottom:1px solid #f4f4f4;
    background: #fff;
    font-size:.48rem;
  }
  .address-form>div >label{
    text-align: left;
  }
  .address-form>div >input,.address-textarea textarea{
    width:8rem;
    font-size:.48rem;
    border:none;
    outline:none;
  }
  .address-form>div.address-textarea{
    height:2.1rem;
  }
  .address-textarea textarea{
    resize: none;
    height:1.4rem;
  }
  .adderss-city input{
    background:url("../../images/my/icon_right.png") no-repeat right center/.17rem .36rem;
  }
  .address-checkBtn{
    height:1.48rem;
    display: flex;
    justify-content: space-between;
    align-items: center;
    padding:0 .3rem;
    margin-top:.3rem;
    background: #fff;
  }
  .address-save{
    margin:.45rem .6rem;
    height: 1.32rem;
    background-color: #de2245;
    border-radius: 1rem;
    line-height:1.32rem;
    text-align: center;
    font-size:.54rem;
    color: #fff;
  }
  /*表单开关样式*/
  .ui-switch {
    position: relative;
    font-size: .16rem;
    width: 1.5rem;
    height: .84rem;
    line-height: .84rem;
  }
  .ui-switch input {
    width: 1.5rem;
    height: .84rem;
    position: absolute;
    top: 0;
    left: 0;
    z-index: 2;
    border: 0;
    background: 0 0;
    -webkit-appearance: none;
    outline: 0
  }
  .ui-switch input:before {
    content: '';
    width: 1.5rem;
    height: .84rem;
    border: 1px solid #dfdfdf;
    background-color: #fdfdfd;
    border-radius: 1rem;
    cursor: pointer;
    display: inline-block;
    position: relative;
    vertical-align: middle;
    -webkit-box-sizing: content-box;
    box-sizing: content-box;
    border-color: #dfdfdf;
    -webkit-box-shadow: #dfdfdf 0 0 0 0 inset;
    box-shadow: #dfdfdf 0 0 0 0 inset;
    -webkit-transition: border .4s,-webkit-box-shadow .4s;
    transition: border .4s,box-shadow .4s;
    -webkit-background-clip: content-box;
    background-clip: content-box
  }

  .ui-switch input:checked:before {
    border-color: #64bd63;
    -webkit-box-shadow: #64bd63 0 0 0 0.16rem inset;
    box-shadow: #64bd63 0 0 0 0.16rem inset;
    background-color: #64bd63;
    transition: border .4s,box-shadow .4s,background-color 1.2s;
    -webkit-transition: border .4s,-webkit-box-shadow .4s,background-color 1.2s;
    background-color: #64bd63
  }
  .ui-switch input:checked:after {
    left: .72rem
  }
  .ui-switch input:after {
    content: '';
    width:.84rem;
    height: .84rem;
    position: absolute;
    top: 53%;
    left: 0;
    -webkit-transform:translateY(-50%);
    border-radius: 100%;
    background-color: #fff;
    -webkit-box-shadow: 0 0.01rem 0.03rem rgba(0,0,0,.4);
    box-shadow: 0 0.01rem 0.03rem rgba(0,0,0,.4);
    -webkit-transition: left .2s;
    transition: left .2s
  }
</style>



